/* components/Navigation.css */
.sidebar {
  width: 130px;
  height: 100%;
  position: fixed;
  background-color: rgb(77, 77, 77);
  color: #fff;
  padding-top: 16px;
  margin-right: 10px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  margin-bottom: 20px;
}

nav li {
  cursor: pointer;
}

nav {
  background-color: #eee;
  padding: 16px;
}

nav a {
  width: fit-content;
  text-decoration: none;
  color: inherit;
  border-radius: 10px;
  padding: 15px;
}

nav a:hover {
  background-color: grey;
}
